import React, { Component } from "react";
import Fade from "react-reveal";
import "../styles/About.css";

class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentImageIndex: 0,
      images: ["profilepic.jpg", "profilepic2.jpg", "profilepic3.jpg"],
    };
  }

  componentDidMount() {
    // Start the slideshow
    this.interval = setInterval(() => {
      this.setState((prevState) => ({
        currentImageIndex:
          (prevState.currentImageIndex + 1) % this.state.images.length,
      }));
    }, 3000); // Change image every 3 seconds
  }

  componentWillUnmount() {
    // Clear the interval when component unmounts
    clearInterval(this.interval);
  }

  render() {
    if (!this.props.data) return null;

    const name = this.props.data.name;
    const bio = this.props.data.bio;
    const city = this.props.data.address.city;
    const state = this.props.data.address.state;
    const email = this.props.data.email;
    const resumeDownload = this.props.data.resumedownload;

    return (
      <section id="about">
        <Fade duration={1000}>
          <div className="row">
            <div className="three columns">
              <div className="slideshow-container">
                <img
                  className={`profile-pic profile-pic-${this.state.currentImageIndex}`}
                  src={`images/${
                    this.state.images[this.state.currentImageIndex]
                  }`}
                  alt={`${name}'s Profile`}
                />
                <div className="slideshow-dots">
                  {this.state.images.map((_, index) => (
                    <span
                      key={index}
                      className={`dot ${
                        index === this.state.currentImageIndex ? "active" : ""
                      }`}
                      onClick={() =>
                        this.setState({ currentImageIndex: index })
                      }
                    />
                  ))}
                </div>
              </div>
            </div>
            <div className="nine columns main-col">
              <h2>About Me</h2>
              <p>{bio}</p>
              <div className="row">
                <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
                    <span>{name}</span>
                    <br />
                    <span>
                      {city}, {state}
                    </span>
                    <br />
                    <span>{email}</span>
                  </p>
                </div>
                <div className="columns download">
                  <p>
                    <a href={resumeDownload} className="button">
                      <i className="fa fa-download"></i>Download Resume
                    </a>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

export default About;
